import React, { Component } from 'react';
import Link from 'gatsby-link';
import styles from './index.module.css';
import menuIcon from '../../assets/images/menu-icon.png';
import closeIcon from '../../assets/images/cross-icon.png';

class Collapse extends Component {
	constructor() {
		super();
		this.state = {
			open: false,
		};

		// this.onClick = this.onClick.bind(this);
	}

	onClick() {
		const { open } = this.state;
		if (open) {
			this.closeMenu();
		} else {
			this.openMenu();
		}
	}

	openMenu() {
		document.getElementById('menu').style.height = '100%';
		this.setState({
			open: true,
		});
	}

	closeMenu() {
		document.getElementById('menu').style.height = '0%';
		this.setState({
			open: false,
		});
	}

	render() {
		return (
  <div style={{ float: 'right' }}>
    <div id="menu" className={styles.overlay}>
      <button
        href=""
        className={`${styles.closeBtn} ${styles.btn}`}
        onClick={() => this.onClick()}
      >
        <img className={styles.closeIcon} src={closeIcon} alt="close-icon" />
      </button>
      <div className={styles.overlayContent}>
        <Link className={styles.link} onClick={() => this.closeMenu()} to="/">
							Home
        </Link>
        <Link className={styles.link} onClick={() => this.closeMenu()} to="/projects/">
							Projects
        </Link>
        <Link className={styles.link} onClick={() => this.closeMenu()} to="/my-files/">
							Files
        </Link>
      </div>
    </div>
    <button className={`${styles.btn} ${styles.menuIcon}`} onClick={() => this.onClick()}>
      {this.state.open ? '' : <img src={menuIcon} alt="menu-icon" />}
    </button>
  </div>
		);
	}
}

export default Collapse;



